
.post{
    margin-bottom: 45px;
    max-width: 400px;
    background-color: white;
    border:1px solid lightgrey;
}
.post_image{
    
    object-fit: contain;
    width:100%;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;


}
.post_avatar{
    margin-right:5px
}

.post_text{
    font-weight: normal;
    padding:20px;
}

.post_header{
    display: flex;
    align-items: center;
    padding:20px;
}

.post_commentbox{
    display: flex;
    margin-top: 10px;
}

.post_input {
    flex:1;
    border:none;
    padding:10px;
    border-top:1px solid lightgray;
}

.post_button{
    flex:0;
    border:none;
    border-top: 1px solid lightgray;
    color:#6082a3;
    background-color: transparent;
}

.post_comments{
    padding:10px;
}